/* Styles *//* Control *//*functions.scss_December/23/2015*//*colors.scss_July/14/2015*//* Reset All */body {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
}

* {
  box-sizing: border-box;
}

h1,h2,h3,h4,h5,h6,p,button {
  font-weight: normal;
  font-style: normal;
  text-decoration: none;
  margin: 0;
  padding: 0;
  border: none;
  outline: 0;
  background: none;
}

a {
  text-decoration: none;
  margin: 0;
  color: inherit;
}

ul,ol,li {
  text-decoration: none;
  padding: 0;
  margin: 0;
  list-style: none;
  display: inline-block;
}

:focus {
  outline: 0;
}/* Mixin Master */.dbi {
  display: block !important;
}

body {
  display: flex;
}/* Init Defaults */body {
  min-height: 100vh;
  width: 100%;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  color: #2B2B2B;
  flex-direction: column;
}

#wrapper {
  width: 100%;
  flex: 1;
  transition: background-color 400ms;
}

a svg,button svg,label svg {
  pointer-events: none;
}

.icon {
  fill: #2B2B2B;
  width: 1.5rem;
  height: 1.5rem;
}

#svgHolder {
  display: none;
}/* Main */main {
  width: 100vw;
  height: 100vh;
  position: relative;
}/* stupid Box */.stupidBox {
  width: 100px;
  height: 100px;
  background-color: #4CD964;
  position: absolute;
  border-radius: 0.125rem;
  transition: all 200ms cubic-bezier(0.4, 0, 1, 1);
}

@media only screen and (min-width: 1025px), screen and (min-device-width: 768px) and (max-device-width: 1024px), screen and (min-width: 641px) and (max-width: 1024px) {
  #wrapper {
    display: flex;
    flex-direction: column;
  }
}
